import React from "react";
import AwesomeSlider from "react-awesome-slider";
import "react-awesome-slider/dist/styles.css";
import { withNavigationHandlers, withNavigationContext } from "react-awesome-slider/dist/navigation";

import { Main } from "./../../scenes/Main";
import { AboutMe } from "./../../scenes/AboutMe";
import { RedditPreview } from "./../../scenes/RedditPreview";

const NavigationSlider = withNavigationHandlers(AwesomeSlider);

const RouterSlider = withNavigationContext(({ fullpage }) => {
  const animation = fullpage.navigation.animation || `foldOutAnimation`;

  return (
    <NavigationSlider
      animation={animation}
      className="c-awesome-slider"
      media={[
          {
            slug: "page-one",
            className: "c-slider-slug",
            children: (<><Main/><AboutMe/></>)
          },
          {
            slug: "page-two",
            className: "c-slider-slug",
            children: <RedditPreview />
          },
      ]}
    />
  );
});

export { RouterSlider };